<template>
	<div class="main-body">
		<div class="header">
			<a href="/">Exchange</a>
		</div>

		<form class="pure-form">
			<div class="head">
				<a id="nav" class="active"></a>
				<b></b>
				<a id="nav" href="/user/#/login"></a>
			</div>
			
			<fieldset class="pure-group">
				<div @click="clickNameInput" class="input-field">
					<label id="form-label">{{name_msg}}</label>
					<input v-model="name" type="text" class="pure-input-1" placeholder="">
				</div>

				<div @click="clickPhoneInput" style="border-top: none;border-bottom: none" class="input-field">
					<label id="form-label">{{email_msg}}</label>
					<input v-model="email" type="text" class="pure-input-1" placeholder="">
				</div>

				<div @click="clickPasswdInput" class="input-field">
					<label id="form-label">{{passwd_msg}}</label>
					<input v-model="password" type="password" class="pure-input-1" placeholder="">
				</div>
		    </fieldset>

		    <a class="foot-menu" href="/#/register_phone"></a>
		    <span @click="checkInfo" id="btn" class="pure-button pure-input-1 pure-button-primary"></span>
		</form>
	</div>
</template>

<script>
import axios from 'axios'
export default {
  data () {
    return {
      name: '',
      email: '',
      password: '',
      name_msg: '',
      email_msg: '',
      passwd_msg: ''
    }
  },
  mounted(){
    document.title = 'Register',
    this.ifLogin()
  },
  methods:{
    ifLogin:function() {
      var self = this;
      axios({
        method: 'get',
        url: '/api/login_or_not/',
        headers: Cookies.get('token')?{'Authorization': 'Token ' + Cookies.get('token')}:{},
      }).then(function(response) {
        window.location.href = '/'
      }).catch(function() {

      })
    },
    checkInfo:function() {
      var self = this;
      var check = true;
      // 
      if (self.name == ''){
        self.name_msg="";
        check = false;
      }
      // 
      var strreg=/^([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+@([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+\.[a-zA-Z]{2,3}$/;
	  if(self.email.search(strreg)==-1) { 
		self.email_msg = "";
		check = false;
	  } 

      if (self.password.length < 6){
        self.passwd_msg = "6";
        check = false;
      }
      // 
      if (check == true){
      	self.Register()
      }
    },
  	Register:function() {
  		var self = this;
  		axios.post('/api/register/', {
  			name: self.name,
  			email: self.email,
  			password: md5(self.password)
  		}).then(function(response) {
  			window.location.href = '/user/#/login'
  		}).catch(function() {
  			self.email_msg = ""
  		})
  	},
    clickNameInput:function() {
      var self = this;
      self.name_msg = ''
    },
    clickPhoneInput:function() {
      var self = this;
      self.email_msg = ''
    },
    clickPasswdInput:function() {
      var self = this;
      self.passwd_msg = ''
    },
  }
}
</script>




