<template>
	<div class="main-body">
		<div class="header">
			<a href="/">Exchange</a>
		</div>
		
		<form class="pure-form">
			<div class="head">
				<a id="nav" href="/user/#/register_phone"></a>
				<b></b>
				<a id="nav" class="active"></a>
			</div>

			<fieldset class="pure-group">
				<div @click="clickName" class="input-field" style="border-bottom: none;">
					<label id="form-label">{{name_msg}}</label>
					<input v-model="username" type="text" class="pure-input-1" placeholder="">
				</div>

				<div @click="clickPassword" class="input-field">
					<label id="form-label">{{password_msg}}</label>
					<input v-model="password" type="password" class="pure-input-1" placeholder="">
				</div>
		    </fieldset>

		    <span @click="checkInfo" id="btn" type="submit" class="pure-button pure-input-1 pure-button-primary"></span>
		</form>
	</div>
</template>

<script>
import axios from 'axios'
export default {
  data () {
    return {
      username: '',
      password: '',
      name_msg: '',
      password_msg: '',
      md_password: ''
    }
  },
  mounted(){
    document.title = 'Login',
    this.ifLogin()
  },
  methods:{
    ifLogin:function() {
      var self = this;
      axios({
        method: 'get',
        url: '/api/login_or_not/',
        headers: Cookies.get('token')?{'Authorization': 'Token ' + Cookies.get('token')}:{},
      }).then(function(response) {
        window.location.href = '/'
      }).catch(function() {

      })
    },
  	clickName:function() {
  		var self = this;
  		self.name_msg = ''
  	},
  	clickPassword:function() {
  		var self = this;
  		self.password_msg = ''
  	},
  	logIn:function() {
  		var self = this;
  		axios.post('/api/login/', {
  			username: self.username,
  			password: md5(self.password)
  		}).then(function(response) {
  			Cookies.set('token', response.data.token,{ expires: 10});
  			window.location.href = "/"
  		}).catch(function(error) {
  			self.name_msg = ''
  		})
  	},
  	checkInfo:function() {
  		var self = this;
  		var check = true;
  		if (self.username == ''){
  			self.name_msg = '';
  			check = false
  		}
  		if (self.password.length < 6){
  			self.password_msg = '6';
  			check = false
  		}
  		if (check == true){
  			this.logIn()
  		}
  	}
  }
}
</script>



